import React from 'react'

function About() {
    function emPvl() {
        let txt1 = document.querySelector('.txt');
        let paraGraph = document.querySelector('.lead');
        paraGraph.innerText = txt1.value;
        txt1.value = "";
    }
    return (
        <div className="container">
            <h1>About page</h1>
            <img className="my-5" src="https://picsum.photos/id/237/400/300" alt="" />
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Rem est repellendus earum natus optio alias, aliquid vel facilis ipsam dolor repellat, illo cupiditate nihil unde placeat sunt? Ullam delectus cumque dolorem possimus distinctio? Inventore, corporis?</p>

            <div className="conditional-wrapper bg-warning text-dark pb-2 mb-3">
                <p className="lead py-2">Type anything in Input and see the value here...</p>
                <input type="text" className="txt" onBlur={emPvl} />
            </div>
        </div>
    )
}



export default About
